*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}
li {
  list-style: none;
}
a,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}
img {
  vertical-align: top;
}
html,
body {
  height: 100%;
  line-height: 1;
  // background-color: #1c1d25;
  -webkit-tap-highlight-color: transparent;
  /* // При тапах убирает прямоугольники фоновые */
  -webkit-font-smoothing: antialiased;
}
html {
  /* по умолчанию */
  font-size: rem(16);
  color: black;
  // font-family: "Montserrat";
}
